import Papa from 'papaparse';

const ratioMap = {
    priceToEarnings: '   (P/E)',
    EPS: '   (EPS)',
    PEG: 'PEG-',
    priceToSales: '   (P/S)',
    priceToBook: '    (P/B)',
    dividendYield: '  (Dividend Yield)',
    dividendPayout: '  (Dividend Payout)',
    ROA: '  (Return On Assets)',
    ROE: '   (Return On Equity)',
    profitMargin: '  (Profit Margin)',
    depthToEquity: '    (Debt To Equity) ',
    currentRatio: '  (Current Ratio)',
    beta: '- (Beta)'
}

export const createCSV = (data) => {
    const headers = ['', ...data.map(({ ticker }) => ticker)]
    const values = Object.keys(ratioMap).map(ratio => [ratioMap[ratio], ...data.map(({ ratios }) => ratios[ratio])]);
    return Papa.unparse([headers, ...values])
}